.bage-block {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 15px;
  left: -40px;
  top: -40px;
  width: 157px;
  height: 157px;
  text-align: center;
  font-size: 20px;
  font-weight: 700;
  background: $accent;
  border-radius: 50%;
  color: white;
  z-index: 3;
  &:before {
    content: "";
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    border: solid 1px #145283;
    right: -5px;
    top: -5px;
  }
  @media (max-width: 992px) {
    right: 10px;
    left: auto;
    width: 116px;
    height: 116px;
    font-size: 16px;
    &:before {
      width: 100%;
      height: 100%;
    }
  }
}

.offer-section,
.top-product-section,
.video-section,
.podcast-section,
.p-membership-section,
.members-stories-section,
.home-blog-section,
.bottom-section,
.masonry-section {
  overflow: hidden;
  padding: 60px 0;
  @media (max-width: 992px) {
    padding: 20px 0;
  }
}

////offer-section////
.offer-section {
  color: $text-gray;
  font-weight: 500;
  h1.header-section {
    font-size: 72px;
    line-height: 1.1;
    text-align: left;
    font-weight: 800;
  }
  h2 {
    margin-bottom: 60px;
    font-size: 20px;
  }
  .btn-accent.btn-gradient {
    margin-bottom: 60px;
  }
  p {
    margin-bottom: 20px;
  }
  .wrapper-image {
    position: relative;
    margin-bottom: 16px;

    &:after {
      content: url("../img/vector-big.png");
      position: absolute;
      left: -160px;
      top: 60px;
    }
    @media (max-width: 992px) {
      &:before {
        left: -140px;
        top: -40px;
      }
      &:after {
        content: url("../img/vector-small.png");
        left: auto;
        right: 0px;
        top: -250px;
      }
    }
  }
  .offer-image {
    position: relative;
    display: block;
    margin: 0 auto;
  }
  @media (max-width: 1140px) {
    h1.header-section {
      font-size: 50px;
    }
    h2 {
      margin-bottom: 30px;
      font-size: 18px;
    }
  }
  @media (max-width: 991px) {
    padding: 30px 0;
    h1.header-section {
      font-size: 36px;
    }
    h2 {
      margin-bottom: 30px;
      font-size: 18px;
    }
    .btn-accent.btn-gradient {
      margin-bottom: 12px;
    }
  }
}

////video-section///
.video-section {
  p {
    font-size: 20px;
    line-height: 2;
    color: $text-gray;
  }
  @media (max-width: 991px) {
    p {
      font-size: 16px;
    }
  }
}

////podcast-section///
.podcast-section {
  color: $text-gray;
  .btn-transparent {
    min-width: 230px;
  }
  .col2 {
    text-align: right;
    @media (max-width: 992px) {
      text-align: center;
    }
  }
  .col3 {
    .btn-accent {
      margin-top: 60px;
    }
  }
  h3 {
    font-size: 32px;
    color: $text;
    font-weight: 700;
    margin-bottom: 24px;
  }
  @media (max-width: 992px) {
    .col2 {
      text-align: center;
    }
    .col3 {
      .btn-accent {
        margin-top: 20px;
      }
    }
    h2 {
      margin-bottom: 0;
    }
    h3 {
      font-size: 20px;
    }
  }
}

////p-membership-section///
.p-membership-section {
  .col1 {
    position: relative;
    .wrap {
      display: flex;
      justify-content: space-between;
      .bage-block {
        position: relative;
        top: 0;
        left: 116px;
        width: 113px;
        height: 113px;
        background-color: #72B346;
        &:after {
          content: "";
          position: absolute;
          bottom: -100%;
          width: 348px;
          height: 151px;
          display: block;
          background: url("../img/vector-pm-big.png");
        }
      }
      img {
        margin-left: 60px;
      }
    }
  }
  .pm-list {
    margin-top: 30px;
    padding-left: 20px;
    display: inline-block;
    text-align: left;
    font-size: 24px;
    font-weight: 700;
    list-style-position: inside;
    list-style: disc;
  }
  @media (max-width: 992px) {
    padding: 20px 0 40px;
    .col1 {
      .wrap {
        justify-content: center;
        .bage-block {
          width: 78px;
          height: 78px;
          left: 30px;
          &:after {
            left: 0;
            width: 112px;
            height: 96px;
            display: block;
            background: url("../img/vector-pm-small.png");
          }
        }
        img {
          width: 195px;
          max-width: 100%;
        }
      }
    }
    .col2 {
      &:after {
        width: 210px;
        height: 165px;
      }
    }
    .pm-list {
      font-size: 16px;
    }
  }
}

///members-stories-section///
.members-stories-section {
  .col1 {
    text-align: right;
    img {
      max-width: 80%;
      border-radius: $border-radius;
    }
  }
  .slider-stories-navigation {
    position: relative;
    margin: 50px 150px;
    width: 150px;
    height: 30px;
    @media (max-width: 1200px) {
      margin: 20px auto;
    }
  }
  .wrap-blockquote {
    padding: 20px;
  }
  .wrapp {
    margin: 10px;
  }
  .blockquote-stories {
    position: relative;
    padding: 60px;
    background-color: white;
    border-radius: $border-radius;
    &:before,
    &:after {
      position: absolute;
      color: #1b75bb;
      font-size: 30px;
      font-family: 'icomoon' !important;
    }
    &:before {
      content: "\e905";
      left: 8px;
      top: -8px;
    }
    &:after {
      content: "\e906";
      right: 8px;
      bottom: -8px;
      color: #1b75bb;
    }
    p {
      position: relative;
      margin-bottom: 80px;
      font-size: 24px;
      line-height: 1.9;
    }
    figcaption {
      font-size: 24px;
      color: $text-gray;
      cite {
        font-style: normal;
      }
    }
  }
  @media (max-width: 1200px) {
    .col1 {
      img {
        max-width: 100%;
      }
    }
    .blockquote-stories {
      padding: 26px 16px;
      p {
        margin-bottom: 24px;
        font-size: 18px;
      }
      figcaption  {
        font-size: 18px;
      }
    }
  }
}

///home-blog-section///
.home-blog-section {
  h3 {
    margin-top: 24px;
    margin-bottom: 24px;
    font-size: 32px;
    font-weight: 800;
  }
  .wrapper-articles {
    position: relative;
    padding-bottom: 50px;
  }
  .home-slider-blog-navigation {
    position: absolute;
    width: 150px;
    right: 20px;
    bottom: 60px;
    @media (max-width: 992px) {
      display: none;
    }
  }
  p {
    font-size: 20px;
    line-height: 2;
    color: $text-gray;
  }
  .btn-accent {
    margin-top: 60px;
  }
  @media (max-width: 991px) {
    .slick-dots {
      bottom: -50px;
    }
    h3 {
      font-size: 20px;
    }
    p {
      font-size: 16px;
    }
  }
}

///bottom-section///
.bottom-section {
  .hog-section {
    margin-bottom: 30px;
    padding-top: 30px;
    background: url("../img/jpit-bg.jpg") 50% 50% no-repeat;
    background-size: cover;
    .hog-logo {
      img {
        width: 300px;
        height: auto;
      }
    }
    h3 {
      margin: 30px auto 40px;
      width: 80%;
      font-size: 120px;
      font-weight: 800;
      line-height: 1.1;
      color: white;
    }
    .wide-btn {
      width: 100%;
      text-align: center;
      font-size: 20px;
      font-weight: 700;
    }
    @media (max-width: 1200px) {
      h3 {
        font-size: 60px;
      }
    }
    @media (max-width: 992px) {
      h3 {
        font-size: 28px;
      }
    }
  }
  .just-pray-it-section {
    margin-top: 40px;
    .wraper-section {
      padding-top: 80px;
      padding-bottom: 80px;
      background: url("../img/jpit-bg.jpg") 50% 50% no-repeat;
      background-size: cover;
      text-align: center;
      color: white;
      .logo-jpit {
        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 50px 50px 50px;
        .line-left, .line-right {
          display: inline-block;
          width: 1px;
          height: 1px;
          background-color: white;
          -webkit-box-flex: 100;
          -webkit-flex-grow: 100;
          -ms-flex-positive: 100;
          flex-grow: 100;
        }
        img {
          width: 500px;
          margin: 0 60px;
        }
      }
      h3 {
        font-size: 40px;
      }
      p {
        font-size: 26px;
      }
      .wide-btn {
        margin-top: 80px;
      }
    }
    @media (max-width: 992px) {
      .wraper-section {
        padding-top: 40px;
        border-radius: $border-radius;
        .logo-jpit {
          .line-left, .line-right {
            display: none;
          }
          img {
            margin: 0 30px;
          }
        }
        h3 {
          font-size: 20px;
        }
        p {
          font-size: 16px;
        }
        .wide-btn {
          margin-top: 40px;
          font-size: 13px;
        }
      }
    }
  }
  @media (max-width: 992px) {
    padding: 30px 0;
  }
}